.desktop__video {
  border-radius: 4px;
  border: 1px solid rgba(15, 27, 94, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.desktop__video video {
  max-width: 680px;
}

.assumptions {
  padding-bottom: 80px;
}
.assumptions .assumptions__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.assumptions .limitation__wrapper {
  margin-top: 80px;
}

.problems {
  padding: 40px 0px 80px 0px;
}
.problems__items {
  display: flex;
  flex-direction: column;
  gap: 64px;
}
.problems__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.problems__item-img {
  margin-bottom: 32px;
}
.problems h2 {
  text-align: center;
  margin-bottom: 40px;
}
.problems img {
  max-width: 680px;
  width: 100%;
}

@media (max-width: 680px) {
  .problems {
    padding-top: 20px;
  }
}
.mobile {
  background-color: #F5F5F7;
  padding: 80px 0px;
  text-align: center;
}
.mobile__video video {
  border-radius: 8px;
}

.logo {
  padding: 80px 0px;
}
.logo h2 {
  text-align: center;
}
.logo__content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.logo__img {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.logo__img--main img {
  max-width: 680px;
  width: 100%;
}
.logo__img--small {
  display: flex;
  gap: 13px;
}
.logo__img--small img {
  max-width: 333px;
  width: 100%;
}
.logo__text {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact {
  padding: 80px 0px;
}

.contact__links {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.contact__links .cs-link {
  display: block;
}
.contact__links .cs-link::after {
  content: " " url(/img/cleaning/arrow-up-right.svg);
}